<template>
  <v-dialog
    :value="value"
    @input="$emit('input', $event)"
    width="auto"
    :fullscreen="$vuetify.breakpoint.xsOnly"
    scrollable
    persistent
  >
    <v-card class="rounded-lg" color="rgba(255, 255, 255, 0.88)" width="100%">
      <v-toolbar color="transparent" class="px-2" flat>
        <v-toolbar-title>編輯使用者</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn @click="resetForm()" icon>
          <v-icon color="primary">mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-container class="py-2" fluid></v-container>
      <v-card-text>
        <v-row>
          <v-col cols="12">
            <v-row class="d-flex align-center">
              <v-col cols="12" class="pb-0">
                <span class="text-body-1">顯示名稱</span>
                <v-spacer></v-spacer>
                <span class="primary--text text-body-2"
                  >您可以自定義使用者在系統內顯示的名稱</span
                >
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="accountData.name"
                  dense
                  outlined
                  hide-details
                  single-line
                  filled
                >
                </v-text-field>
              </v-col>
            </v-row>
            <v-row class="d-flex align-center">
              <v-col cols="12" class="pb-0">
                <span class="text-body-1">Email</span>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="accountData.email"
                  dense
                  outlined
                  hide-details
                  single-line
                  filled
                  disabled
                >
                  <template v-slot:append>
                    <v-container fill-height fluid>
                      <v-row class="d-flex align-center">
                        <v-icon small class="primary--text px-1"
                          >mdi-check-circle-outline</v-icon
                        >
                        <span class="text-body-2 primary--text d-none d-sm-flex"
                          >已驗證</span
                        ></v-row
                      >
                    </v-container>
                  </template>
                </v-text-field>
              </v-col>
            </v-row>
            <v-row class="d-flex align-center">
              <v-col cols="12" class="pb-0">
                <div
                  class="d-flex align-center pointer"
                  @click="expand = !expand"
                >
                  <span class="text-body-1">角色</span>
                  <v-spacer></v-spacer>
                  <span class="d-flex align-center primary--text text-body-2"
                    >說明<v-icon color="primary">mdi-chevron-down</v-icon></span
                  >
                </div>
                <v-expand-transition>
                  <v-card
                    v-show="expand"
                    color="transparent"
                    class="mx-auto my-3"
                    flat
                  >
                    <p class="mb-2">分析師</p>
                    <p class="mb-2">內容管理員</p>
                    <p class="mb-2">系統管理員</p>
                  </v-card>
                </v-expand-transition>
              </v-col>
              <v-col cols="12">
                <v-select
                  :items="roleList"
                  v-model="accountData.role"
                  item-text="text"
                  item-value="type"
                  dense
                  outlined
                  hide-details
                  single-line
                  filled
                ></v-select>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions class="px-6">
        <v-container>
          <v-row>
            <v-col cols="6" class="ps-0">
              <v-btn
                color="rgb(255, 114, 87)"
                class="my-btn py-5"
                depressed
                block
                dark
                @click="submit()"
                >送出</v-btn
              >
            </v-col>
            <v-col cols="6" class="pe-1">
              <v-btn
                color="rgba(0, 0, 0, 0.36)"
                class="my-btn py-5"
                depressed
                block
                dark
                @click="resetForm()"
                >取消</v-btn
              >
            </v-col>
          </v-row>
        </v-container>
      </v-card-actions>
      <v-container class="py-2" fluid></v-container>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    value: Boolean,
    index: Number,
    data: Object,
  },
  watch: {
    data: function () {
      this.accountData = JSON.parse(JSON.stringify(this.data));
    },
  },
  data() {
    return {
      expand: false,
      accountData: {},
      roleList: [
        { type: 0, text: "分析師" },
        { type: 1, text: "內容管理員" },
        { type: 2, text: "系統管理員" },
      ],
    };
  },
  methods: {
    resetForm() {
      this.accountData = JSON.parse(JSON.stringify(this.data));
      this.$emit("input", false);
    },
    submit() {
      const rtn = {
        index: this.index,
        newItem: this.accountData,
      };
      this.$emit("update", rtn);
    },
  },
};
</script>

<style scoped>
.pointer {
  cursor: pointer;
}

.my-btn {
  box-shadow: 1px 1px 0px hsl(0, 0%, 5%), 2px 2px 0px hsl(0, 0%, 5%),
    3px 3px 0px hsl(0, 0%, 5%);
}
</style>
